import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
import {useState, useEffect, useRef} from 'react';
const data = require('./Diary.json');
function generateDiary(o1, o2, o3){
        
    var dataClone = JSON.parse(JSON.stringify(data))
    let template = dataClone.template.split(','), quotes = dataClone.quotes.sort(() => Math.random() - 0.5);
    delete dataClone.template;
    let extract = (a) => typeof a === "string" ? a : a[Math.floor(Math.random() * a.length)]
    for(let i in dataClone) dataClone[i] = dataClone[i].sort(() => Math.random() - 0.5)
    let string = template.map(a => extract(dataClone[a].pop())).join('')
    while(string.includes("quote")) string = string.replace("quote", quotes.pop())
    string = string.replaceAll('運動會', o1.current.querySelector('input').value)
    .replaceAll('接力時掉棒', o2.current.querySelector('input').value)
    .replaceAll('進入總決賽', o3.current.querySelector('input').value)
    .replaceAll('，。', "。").replaceAll('？。', "。")
    return string
}

function Diary({clicked, text, setText}){
    
    const o1 = useRef(), o2 = useRef(), o3 = useRef()
    useEffect(() => setText(generateDiary(o1, o2, o3)), [clicked])
    return (<>
    
    <TextField id="outlined-basic" label="主題" variant="outlined" defaultValue="籃球比賽" sx={{width: '100%'}} ref={o1}/>
    <TextField id="outlined-basic" label="令你難過的事" variant="outlined" defaultValue="輸掉決賽" ref={o2} sx={{width: '100%', marginTop: 2}}/>
    <TextField id="outlined-basic" label="令你高興的事" variant="outlined" defaultValue="進入總決賽" ref={o3} sx={{width: '100%', marginTop: 2}}/>
    <Typography variant="body" component="div" sx={{mt: 2, mb: 6}} id="text">
        {text}
    </Typography>
    </>);
}
export default Diary